@import 'sass/vars.scss';

.App {
  // overflow-x: hidden;
  .Roseblue {
    color: $text;
    width: 100%;
    &.Left {
      z-index: 10;
      position: fixed;
      top: 0;
      .buoy {
        position: absolute;
        top: 0;
        left: 0;
        background: $translucent-bg;
        width: 35%;
        height: 3rem;
        .clip {
          position: absolute;
          background: $translucent-bg;
          top: 0;
          right: -5rem;
          width: 5rem;
          height: 3rem;
          clip-path: url(#left);
        }
      }
      .title {
        position: relative;
        z-index: 10;
        padding-left: 1rem;
        line-height: 3rem;
        font-size: 2rem;
        font-weight: 400;
      }
    }
    &.Right {
      position: sticky;
      top: 0;
      z-index: 2;
      text-align: right;
      .buoy {
        position: absolute;
        top: 0;
        right: 0;
        background: $translucent-bg;
        width: calc(65% - 5rem);
        height: 3rem;
        .clip {
          position: absolute;
          background: $translucent-bg;
          top: 0;
          left: -5rem;
          width: 5rem;
          height: 3rem;
          clip-path: url(#right);
        }
      }
      .nav {
        position: relative;
        z-index: 11;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 60%;
        margin-left: auto;
        line-height: 3rem;
        .nav-item {
          padding-left: 1rem;
          padding-right: 1rem;
          &:last-child {
            padding-right: 0;
          }
        }
      }
    }
  }
  .Mast-text {
    z-index: 0;
    position: relative;
    color: $text;
    height: calc(100vh - 3rem);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content: center;
    .text-container {
      padding-left: 1rem;
      padding-right: 1rem;
    }
    h1 {
      padding-top: 10rem;
      font-size: 2.5rem;
    }
  }
  .Hero-container {
    max-width: 100%;
    overflow-x: hidden;
    position: absolute;
    top: 0;
    z-index: 0;
    left: 0;
    width: 100%;
  }
  .container {
    width: 100%;
    max-width: calc(1400px - 2rem);
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 3600px) {
      max-width: calc(1800px - 2rem);
    }
    @media screen and (min-width: 4800px) {
      //fuck you if you browse at this width btw
      max-width: 100%;
    }
    @media screen and (max-width: 1400px) {
      max-width: calc(1400px - 5rem);
      width: calc(100% - 3rem);
      padding-left: 1.5rem;
      padding-right: 1.5rem;
    }
  }
  .Who-We-Are {
    position: relative;
    z-index: 0;
    width: 100%;
    background: $translucent-bg;
    padding-top: 5rem;
    padding-bottom: 5rem;
    text-align: center;
    h2 {
      line-height: 1.5;
    }
  }
  .What-We-Do {
    background: $text;
    color: $bg;
    padding-top: 5rem;
    padding-bottom: 5rem;
    .title {
      text-align: center;
      font-weight: 700;
    }
  }
}
